.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    gap: 5px;

    button {
        width: 25px;
        height: 25px;
        border: none;
        cursor: pointer;

        /*  ( ) */
        &.first,
        &.prev,
        &.next,
        &.last {
            background-image: url("../../public/img/common/bg_pageination.png");
            background-repeat: no-repeat;
            background-size: cover;

            &.first {
                background-position: 0 0; /*     */
            }

            &.prev {
                background-position: 0 -25px; /*     */
            }

            &.next {
                background-position: 0 -50px; /*     */
            }

            &.last {
                background-position: 0 -75px; /*     */
            }

            /*     */
            color: transparent;
            text-indent: -9999px;
            overflow: hidden;
            white-space: nowrap;
        }
    }

//    
    .page {
        width: 25px;
        height: 25px;
        // background-color: transparent; 
        background-image: none;
        text-align: center;
        line-height: 25px;
        cursor: pointer;
        color: #333; 
        font-size: 14px; 
        font-weight: bold;

        /*   */
        &.active {
            background-color: grey; 
            color: #ffffff;
            border-radius: 4px;
        }
    }
}
